<template><section>
<h2 id="tong-zhi"><a class="header-anchor" href="#tong-zhi" aria-hidden="true">#</a> 通知</h2>
<blockquote>
<p>悬浮出现在页面角落，显示全局的通知提醒消息</p>
</blockquote>
<hr>
<h2 id="shi-yong-fan-li"><a class="header-anchor" href="#shi-yong-fan-li" aria-hidden="true">#</a> 使用范例</h2>
<blockquote>
<p>普通使用</p>
</blockquote>
<vs-SourceBlock>
                    <div><vs-Button type="primary" @click="onShowNotify">普通通知</vs-Button>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowNotify"</span>&gt;</span>普通通知<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
</code></pre>
</div></vs-SourceBlock><blockquote>
<p>模板使用</p>
</blockquote>
<vs-SourceBlock>
                    <div><vs-Button type="primary" @click="onShowSuccessNotify">成功</vs-Button>
<vs-Button type="primary" @click="onShowInfoNotify">普通</vs-Button>
<vs-Button type="primary" @click="onShowWarningNotify">警告</vs-Button>
<vs-Button type="primary" @click="onShowErrorNotify">错误</vs-Button>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowSuccessNotify"</span>&gt;</span>成功<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowInfoNotify"</span>&gt;</span>普通<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowWarningNotify"</span>&gt;</span>警告<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowErrorNotify"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
</code></pre>
</div></vs-SourceBlock></section></template>

<script>
export default {
    methods: {
        onShowNotify() {
            this.$notify({ message: '这是一个消息' })
        },
        onShowSuccessNotify() {
            this.$notify({ message: '这是一个消息', type: 'success', iconable: true })
        },
        onShowInfoNotify() {
            this.$notify({ message: '这是一个消息', type: 'info', iconable: true })
        },
        onShowWarningNotify() {
            this.$notify({ message: '这是一个消息', type: 'warning', iconable: true })
        },
        onShowErrorNotify() {
            this.$notify({ message: '这是一个消息', type: 'error', iconable: true })
        }
    }
}
</script>