















import Vue from 'vue'
import { Component } from 'vue-property-decorator'

@Component
export default class SourceBlock extends Vue {
  isShowSource: boolean = false

  get showclass() {
    return [
      'btn',
      { [`active`]: !this.isShowSource }
    ]
  }

  get sourclass() {
    return [
      'btn',
      { [`active`]: this.isShowSource }
    ]
  }
  ChangeShowBlockStat(flag: boolean) {
    this.isShowSource = flag
  }
}
