<vs-Pagination layout="prev, pager, next" :page-size="10" :total="1000"></vs-Pagination>
<vs-Pagination :page-size="20" :pager-count="11" layout="prev, pager, next" :total="1000">
</vs-Pagination>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Pagination</span>
