<vs-SourceBlock>
                    <div><vs-Pagination small="" background="" :page-size="10" layout="prev, pager, next" :total="50">
</vs-Pagination>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Pagination</span>
  <span class="hljs-attr">small</span>
