<template><section>

<h2 id="tu-biao"><a class="header-anchor" href="#tu-biao" aria-hidden="true">#</a> 图标</h2>
<blockquote>
<p>图标组件，来源：<a href="https://github.com/ionic-team/ionicons">ionicons</a></p>
</blockquote>
<hr>
<h2 id="shi-yong-fan-li"><a class="header-anchor" href="#shi-yong-fan-li" aria-hidden="true">#</a> 使用范例</h2>
<blockquote>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"33px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vs-Icon</span>&gt;</span>
</code></pre>
</blockquote>
<h2 id="tu-biao-yu-lan"><a class="header-anchor" href="#tu-biao-yu-lan" aria-hidden="true">#</a> 图标预览</h2>
<div style="text-align: center">
    <input type="text" class="md_icon_input" v-model="value" placeholder="搜索">
</div>
<div class="md_icon_show" style="width: 80%;margin: auto;margin-top:20px;">
    <ul>
      <li v-for="(name, index) in icons" :key="index">
        <vs-Icon :type="name" size="30px"></vs-Icon>
        <span>{{ name }}</span>
      </li>
    </ul>
</div>
</section></template>
<style scoped>
    .md_icon_input {
        width: 70%;
        height: 25px;
        padding: 3px;
        margin: auto;
        margin-top: 10px;
        border-radius: 6px;
        border: solid 1px #eeeeee;
        font-size: 14px;
        text-align: center;
    }
    .md_icon_show ul li {
      float: left;
      width: 16%;
      text-align: center;
      height: 110px;
      line-height: 60px;
      color: #666;
      font-size: 13px;
      border: 1px solid #eee;
      padding-top: 20px;
    }

    .md_icon_show span {
      display: block;
      line-height: normal;
      vertical-align: middle;
      color: #aaaaaa;
    }
</style>
<script>
  var iconList = require('../demo/icon.json');

  export default {
    data() {
      return {
        value: '',
      }
    },
    computed: {
      icons: function() {
        let value = this.value
        return iconList.filter(function(item) {
          return item.toLowerCase().indexOf(value.toLowerCase()) != -1
        })
      }
    }
  }
</script>