<template><section>
<h2 id="xiao-xi-ti-shi"><a class="header-anchor" href="#xiao-xi-ti-shi" aria-hidden="true">#</a> 消息提示</h2>
<blockquote>
<p>常用于主动操作后的反馈提示</p>
</blockquote>
<hr>
<h2 id="shi-yong-fan-li"><a class="header-anchor" href="#shi-yong-fan-li" aria-hidden="true">#</a> 使用范例</h2>
<blockquote>
<p>普通使用</p>
</blockquote>
<vs-SourceBlock>
                    <div><vs-Button type="primary" @click="onShowMessage">消息提示</vs-Button>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowMessage"</span>&gt;</span>消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
</code></pre>
</div></vs-SourceBlock><blockquote>
<p>模板使用</p>
</blockquote>
<vs-SourceBlock>
                    <div><vs-Button type="primary" @click="onShowSuccessMessage">成功</vs-Button>
<vs-Button type="primary" @click="onShowInfoMessage">普通</vs-Button>
<vs-Button type="primary" @click="onShowWarningMessage">警告</vs-Button>
<vs-Button type="primary" @click="onShowErrorMessage">错误</vs-Button>
</div>
                    
                    <div slot="source"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowSuccessMessage"</span>&gt;</span>成功<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowInfoMessage"</span>&gt;</span>普通<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowWarningMessage"</span>&gt;</span>警告<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">vs-Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onShowErrorMessage"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">vs-Button</span>&gt;</span>
</code></pre>
</div></vs-SourceBlock></section></template>

<script>
export default {
    methods: {
        onShowMessage() {
            this.$message({ message: '这是一个消息' })
        },
        onShowSuccessMessage() {
            this.$message({ message: '这是一个消息', type: 'success', iconable: true })
        },
        onShowInfoMessage() {
            this.$message({ message: '这是一个消息', type: 'info', iconable: true })
        },
        onShowWarningMessage() {
            this.$message({ message: '这是一个消息', type: 'warning', iconable: true })
        },
        onShowErrorMessage() {
            this.$message({ message: '这是一个消息', type: 'error', iconable: true })
        }
    }
}
</script>